  background-color: #1b2d37; }

.promo__description {
  display: flex;
  flex-direction: row;
  max-width: 1580px;
  margin: 0 auto;
  padding: 0 71px;
  background: url("../img/board-desktop@1x.png") 868px 175px no-repeat, url("../img/promo-polygon.png") 783px 55px no-repeat;
  color: #ffffff; }

.promo__description-text {
